@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 0;
  background: #4d4d4d;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}
.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.white-text {
  color: white;
}

.gray-text{
  color: #4d4d4d;
}
.block{
  display: block;
}
.flex-column{
  display: flex;
  flex-direction: column;
}
.width-60{
  width: 60%;
}
.yellow-text{
  color: #F8DF00;
}
.m-1 {
  margin: 1rem;
}
.m-05{
  margin: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.m-b-2{
  margin-bottom: 2rem;
}
@media (max-width: 600px) {
  h2,
  h3 {
    font-size: 1.25rem;
  }
}
@media (max-width: 768px) {
 .users-mobile{
   width: 60% !important;
 }

}

.fade-in {
  animation: fadeIn ease 3s;
  -webkit-animation: fadeIn ease 3s;
  -moz-animation: fadeIn ease 3s;
  -o-animation: fadeIn ease 3s;
  -ms-animation: fadeIn ease 3s;
}

.fade-in-faster {
  animation: fadeIn ease 1.5s;
  -webkit-animation: fadeIn ease 1.5s;
  -moz-animation: fadeIn ease 1.5s;
  -o-animation: fadeIn ease 1.5s;
  -ms-animation: fadeIn ease 1.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.forget_password {
  width: 30%;
  margin: auto;
}

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #ff0055;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}
.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #f8df00;
  border-color: #292929;
  color: #292929;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}
@media (max-width:950px){
  .nav-links li{
    margin: 1rem 0.5rem;
  }
  .nav-links a{
    padding: 0.1rem;
  }
}
.search-bar__form {
  display: flex;
  text-align: center;
  justify-content: center;
  width: 150px;
  line-height: 100%;
  height: 100%;
}

.search-bar__text {
  margin: 0;
  width: 110px;
  height: 40px;
  font-size: 1rem;
  padding: 5px;
  border-radius: 10px 0 0 10px;
  border: 1px solid white;
}

.search-bar__submit {
  margin: 0;
  width: 40px;
  height: 40px;
  padding: 5px;
  border-radius: 0 10px 10px 0;
  border: 1px solid white;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}
@media (max-width:450px){
  .main-navigation__title{
    font-size: 1.25em;
  }
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
  z-index: 4;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.search__page {
  display: flex;
  margin: 0;
  padding: 0;
  text-align: center;
  justify-content: center;
}
.modal {
  z-index: 100;
  position: fixed;
  top: 20vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}
@media (max-width: 600px) {
  .modal {
    top: 5vh;
  }
}
.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #2a006e;
  color: white;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 0.5rem 0.5rem;
  margin-top: 2.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff0055;
  border-radius: 4px;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

@media (max-width:600px){
  .button--mobile{
    padding: 0.15rem;
    font-size: 0.9rem;
    display: block;
    margin-top: 0.25em;
  }
}
.search-headers {
  color: #ff0055;
  font-size: x-large;
  text-decoration: underline;
}
.search-result__name {
  color: yellow;
  font-size: larger;
  padding: 5px;
}

.search-result__name:hover {
  background-color: yellow;
  color: black;
  border-radius: 20px;
}

a:link {
  text-decoration: none;
}
.card {
  position: relative;
   box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  overflow: hidden;
  background: white;
  padding: 1rem;
}

/* general */
.messages__card {
  display: flex;
  justify-content: space-between;
}

/* Contacts */
.contacts__container {
  width: 30%;
  scroll-behavior: smooth;
}

.header {
  background: #f8df00;
  color: #333;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  cursor: pointer;
}

.activatedContact {
  box-shadow: 3px 3px #666;
  background: rgba(0, 0, 0, 0.13);
}

.link-text {
  color: #ff0055;
  width: 70%;
}

.user-item__content {
  padding: 0 !important;
  margin: 3% 0;
  display: flex;
  justify-content: space-between;
  max-height: 6em;
}

.user-item__content button {
  border-radius: 20%;
  height: 50%;
  border: none;
}

.user-item__content button:hover {
  background: #ff0055;
}

.cardWidth {
  width: 92%;
}

/* Messages */
.message__box {
  width: 68%;
}

.messages__card h2 {
  padding: 20px 30px;
  margin: 0;
}

.msgsContainer {
  overflow-y: scroll;
  height: 30vh;
}
.mobile-hidden {
  display: none;
}

@media (max-width: 768px) {
  .mobile-hidden {
    display: inline;
    color: #ff0055;
    text-decoration: underline;
  }
  .contact__container-hidden {
    display: none;
  }
  .message__box-hidden {
    display: none;
  }
  .message__box-mobile {
    width: 100vw;
    padding: 1rem;
  }
  .contact__container-mobile {
    width: 100vw;
  }
}
.card {
  padding: 0 !important;
}

.form-control {
  margin: 1rem 0;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}
.mContainer {
  background: beige;
  padding: 5%;
  color: darkblue;
  padding: 0 2%;
  margin: 1% 0;
  word-wrap: break-word;
}

.myMsg {
  justify-content: flex-end;
  text-align: right;
  word-wrap: break-word;
}

.msgInfo {
  display: flex;
  justify-content: space-between;
  justify-items: stretch;
  text-align: right;
  margin: 0;
}

.toRyt {
  justify-content: flex-end;
}

.toLeft {
  justify-content: flex-start;
}

.mContainer h3 {
  margin: 0;
}

.mContainer button {
  border: 1px solid saddlebrown;
  cursor: pointer;
  border: none;
  color: maroon;
}

.msgInfo p {
  color: #999;
  font-size: 0.8rem;
  margin: 0 1%;
}

.mContainer button:hover {
  background: maroon;
  color: white;
  border-radius: 50px;
}

.avatar {
  width: 100%;
  height: 100%;
  display: flex; 
  justify-content: center;
  align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
